import React from 'react';
import { Table, Statistic, Icon, Tag, message } from 'antd';
import './index.less';
import Utils from "../../utils";

const CheckableTag = Tag.CheckableTag;

const columns = [
  {
    title: '',
    dataIndex: 'mname'
  },
  {
    title: '',
    dataIndex: 'amount'
  },
  {
    title: '',
    dataIndex: 'type'
  }
];
export default class Detail extends React.Component {
  state = {
    menu: this.props.location.state,
    isLike: false,
    value: 1163,
    starColor: '#52c41a',
    isCollect: false
  };
  UNSAFE_componentWillMount(){
    this.getCollection()
  }

  getCollection = () => {
    const menuName = this.state.menu.name
    let isCollect = this.state.isCollect
    let starColor = this.state.starColor
    if (localStorage.getItem(menuName) != null) {
      isCollect = true
      starColor = '#FDDA04'
    }
    this.setState({
      isCollect,
      starColor
    })
  }
  // 
  renderMenuStep = (data) => {
    return (data.map((step, index) => (
      <div className="process-item" key={index}>
        <img className="process-item_img" src={step.pic} alt="" />
        <div className="process-item_des">
          <div className="step">{index+1}</div>
          <p className="describe" dangerouslySetInnerHTML={{__html: step.pcontent}}></p>
        </div>
      </div>
    )))
  }

  getLike = () => {
    let value = this.state.value
    let isLike = this.state.isLike
    if (!this.state.isLike) {
      value++
      isLike = true
    } else {
      value--
      isLike = false
    }
    this.setState({
      value,
      isLike
    })
  }
  // 
  handleCollect = () => {
    let starColor = this.state.starColor
    let isCollect = this.state.isCollect
    const menu = JSON.stringify(this.state.menu)
    const menuName = this.state.menu.name
    if (isCollect === false) {
      starColor = '#FDDA04'
      isCollect = !isCollect
      localStorage.setItem(menuName, menu)
    } else {
      starColor = '#52c41a'
      isCollect = !isCollect
      localStorage.removeItem(menuName)
    }
    this.setState({
      starColor,
      isCollect
    })

    message.success((isCollect ? '' : ''), 1)

  }

  render() {
    const menu  = this.state.menu
    const tags = Utils.toArray(menu.tag).slice(0 ,6)
    const data = Utils.getRealType(menu.material)
    const process = menu.process

    return (
      <div className="wrapper">
        <div className="info">
          <img
            className="img"
            src={menu.pic}
            alt=""
          />
          <div className="introduce">
            <h2 className="menu-name">
              <span>{menu.name}</span>
            </h2>
            <h4>{menu.preparetime}</h4>
            <h4>{menu.cookingtime}</h4>
            <h4>{menu.peoplenum}</h4>
            <div className="tags">
              <h5 style={{ marginRight: 8, display: 'inline' }}>:</h5>
              {tags.map(tag => (
                <CheckableTag
                  key={tag}
                >
                  {tag}
                </CheckableTag>
              ))}
            </div>
            <div className="statistic">
              <Statistic title="" value={112893} className="statistic-item"/>
              <Statistic
                className="statistic-item"
                title=""
                value={this.state.value}
                prefix={<Icon type="like" onClick={this.getLike} className="like"/>}
              />
              <Icon type="star" theme="twoTone" twoToneColor={this.state.starColor} style={{ fontSize: 30 }} onClick={this.handleCollect}/>
            </div>
          </div>
        </div>
        <div className="describe" >
          <span className="marks"></span>
          <span dangerouslySetInnerHTML={{ __html: menu.content }}></span>
          <span className="marks"></span>
        </div>

        <div className="ingredients">
          <h3><Icon className="icon" type="shopping" theme="twoTone" twoToneColor="#52c41a"/></h3>
          <Table columns={columns} dataSource={data} size="small" pagination={false}/>
        </div>
        <div className="process">
          <h3><Icon className="icon" type="shopping" theme="twoTone" twoToneColor="#52c41a" />{menu.name}</h3>
          {this.renderMenuStep(process)}
        </div>
      </div>
    );
  }
}
